/**
 * 
 */
import { useEffect, useState, useMemo, useRef } from 'react';
import { Button, Tag } from 'antd';
import {
  FooterToolbar,
  ModalForm,
  PageContainer,
  ProDescriptions,
  ProFormText,
  ProFormTextArea,
  ProTable,
} from '@ant-design/pro-components';
export default () => {
  const actionRef = useRef();
  let statusList = [
    {
      label: '已处理',
      value: '1',
    },
    {
      label: '未处理',
      value: '2',
    },
    {
      label: '已取消',
      value: '3',
    },
  ];
  let columns = [
    {
      title: '序号',
      valueType: 'index',
      width: 80,
    },
    {
      title: '客户',
      dataIndex: 'ssstr',
      width: 150,
      ellipsis: true,
    },
    {
      title: '审批编号',
      dataIndex: 'ssstr',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '订单编号',
      dataIndex: 'ssstr',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '类型',
      dataIndex: 'ssstr',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '金额',
      dataIndex: 'ssstr',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '时间',
      dataIndex: 'ssstr',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '状态',
      dataIndex: 'status',
      width: 150,
      ellipsis: true,
      search: false,
      valueType: 'select',
      fieldProps: {
        options: statusList,
        showSearch: true,
        allowClear: true,
        showArrow: true,
      },
      render: (text: any, record: any) => (
        <>
          {
            record.status == '1' ? <Tag color="green">已处理</Tag> :
              record.status == '2' ? <Tag color="blue">未处理</Tag> :
                record.status == '3' ? <Tag color="red">已取消</Tag> : ''
          }
        </>
      ),
    },
    {
      title: '操作',
      dataIndex: 'ssstr',
      width: 150,
      ellipsis: true,
      search: false,
      render: (text: any, record: any) => (
        <Button
          type="link"
          onClick={() => { }}
        >
          查看
        </Button>
      ),
    },

  ];
  return (
    <>
      <ProTable
        headerTitle="查询表格"
        scroll={{ x: 'max-content' }}
        actionRef={actionRef}
        rowKey="key"
        search={{
          labelWidth: 120,
        }}
        toolBarRender={() => [
          <Button
            type="primary"
            key="primary"
            onClick={() => {
              handleModalVisible(true);
            }}
          >
            新建
          </Button>,
        ]}
        request={(params) => {
          // console.log(params);
          return Promise.resolve({
            data: [
              {
                status: '1',
                ssstr: '测试测试测试',
              },
              {
                status: '2',
                ssstr: '测试测试测试',
              },
              {
                status: '3',
                ssstr: '测试测试测试',
              },
            ],
            success: true,
            total: 3
          });
        }}
        columns={columns}
        rowSelection={{
          onChange: (_, selectedRows) => {

          },
        }}
      />
    </>
  );

};